@charset "UTF-8";
/* Reset */
* {margin:0;padding:0;box-sizing:border-box;font:inherit;color:inherit;flex-shrink:0;vertical-align: baseline;border:0;}
html {-moz-text-size-adjust: 100%;-o-text-size-adjust: 100%;-webkit-text-size-adjust: 100%;text-size-adjust: 100%;font-size: 106.25%;line-height: 1.52632;height:100%;}
body {min-width: 320px;}
table {border-collapse:collapse;border-spacing:0}
body {width:100%;overflow-x:hidden;word-break:keep-all;overflow-wrap:break-word;-webkit-tap-highlight-color:transparent;line-height: 1;}
ol,ul {list-style: none;}
a {text-decoration: none;}
a:link, a:hover, a:active, a:visited {text-decoration: none;-webkit-tap-highlight-color:transparent;}
button {background-color:transparent;outline:none;border:none;cursor:pointer;}
figure {line-height: 0;}
a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {border:0;font-size:100%;font:inherit;margin:0;padding:0;vertical-align:baseline}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {display:block}
input {-webkit-border-radius: 0;-webkit-appearance: none;}
img {display:block;width:100%;-webkit-user-drag:none;}
blockquote, q {quotes:none}
blockquote:after, blockquote:before, q:after, q:before {content:"";content:none}
table {border-collapse:collapse;border-spacing:0}

/* Font - Noto Sans Korean */
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');
html {font-family: -apple-system, BlinkMacSystemFont, "Apple SD Gothic Neo", "Noto Sans KR", "Apple Color Emoji", sans-serif;}
.font-notoSans {font-family: 'Noto Sans KR', sans-serif;}